
import { Player, now, loaded } from 'tone'
import ITonerSynth from '../ITonerSynth';
export default class implements ITonerSynth {
    id: string = 'Gong'
    synth: Player
    volume: number = 0.75
    constructor() {
        this.synth = new Player("https://tonejs.github.io/audio/berklee/gong_1.mp3").toDestination();
    }
    play() {
        loaded().then(() => {
            console.log(this.synth)
            this.synth.start(0);
        });
    }
    setVolume() { }
}
